<template>
    <v-app-bar app color="primary" height="80">
        <active-menu-button v-model="stateMenu"/>
        <v-spacer></v-spacer>
        <v-card-title>Eva Muñoz</v-card-title>
        <v-img max-width="60" src="@/assets/logo.png"></v-img>
    </v-app-bar>
</template>

<script>
    import ActiveMenuButton from './components/ActiveMenuButton.vue'

    export default {
        name: 'GeneralNavigationBar',
        props: {
            menuState: {
                type: Boolean,
                required: true
            }
        },
        components: {
            ActiveMenuButton
        },
        computed: {
            stateMenu:{
                get(){
                    return this.menuState
                },
                set(value){
                    setTimeout(() => {
                        if(value) this.$emit('menu-active')
                        if(!value) this.$emit('menu-inactive')
                    }, 1);
                }
            }
        },
    }
</script>